/* color scheme sourced from http://khroma.co/generator/ */
:root {
  --bright-turquoise: #2aefcb;
}
* {
  box-sizing: border-box;
}

body {
  font-family: Arial;
  margin: 0;
}

main {
  display: flex;
  /* Forces elements to the next line if they don't fit */
  flex-wrap: wrap;
  margin-top: 2%;
}

.main-header {
  /* Forces elements to the next line if they don't fit */
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 60px;
  /* Added background image for texture */
  background-image:url(./assets/wood-blue.jpg);
  color: Black;
}
/* I labeled it 'nav'
to separate it from my other selectors*/
.main-header nav {
padding-top: 15px;
/* Aligns it to the right */
float: right;
font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
font-size: 20px;
}

/* Navigation lists */
.main-header nav ul {
list-style-type: none;
}

.main-header nav ul li {
display: inline-block;
margin-left: 25px;
}


.card {
  flex: 1 0 33%;
  flex-grow: 3;
  padding: 10px;
  text-align: left;
  border-style: solid;
  border-width: 1px;
}

/* Tutor helped me add opacity and cursor to hover */
.card:hover{
  opacity: 0.5;
  cursor: pointer;
  box-shadow: inset 0px 0px 8px var(--bright-turquoise), 0 0 15px var(--bright-turquoise);
}

.card footer {
  padding: 20px;
  background: #87CEFA;
  color: Black;
}

.card img {
  width: 100%;
  min-width: 250px;
}
